:root {
  --black-color: #3c3c3c;
  --red-color: #bc002d;
  --container-width: 1280px;
  --thlol: 4px;
  --thtlol: translateY(-40%);
}

html {
  height: 100%;
}
* {
  padding: 0;
  margin: 0;
}
body {
  font-size: 16px;
  font-family: Nunito, sans-serif;
  background-color: #fff;
  color: var(--black-color);
  position: relative;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow-y: auto;
  min-height: 100vh;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
button {
  cursor: pointer;
  font-family: Nunito, sans-serif;
}
.container {
  margin: 0 auto;
  position: relative;
  max-width: var(--container-width);
  width: 94%;
  padding-left: 2%;
  padding-right: 2%;
  display: flex;
  flex-direction: column;
}

@media (max-width: 767px) {
  .container {
    margin: 0;
  }
}
